.header {
  top: 0;
  position: fixed;
  width: 100%;
  padding: 20px 0;
  z-index: 50;
  background-color: $primary-bg;

  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__main-nav {
    margin: 0 auto;
  }

  &__links {
    margin-left: auto;
    display: flex;
    gap: 32px;
    align-items: center;
  }
}
